import { Injectable } from '@angular/core';

declare var NoSleep:any;

@Injectable()
export class NoSleepProvider {
  private noSleep = null;
  private enabled = false;
  constructor() {
    this.noSleep = new NoSleep();
  }

  enable() {
    this.noSleep.enable();
    this.enabled = true;
  }
  disable() {
    this.noSleep.disable();
    this.enabled = false;
  }
  isEnabled() :boolean {
    return this.enabled;
  }
}



