import { Injectable } from '@angular/core';
import { Platform } from 'ionic-angular';

declare var pjXML:any;
const CSSAttrs = [
  'font-size', 'font-family', 'line-height', 'text-align', 'margin-left', 'margin-top'
];

@Injectable()
export class VisualComposerProvider {
  constructor(private pltfrm: Platform) { }

  getPlatformMode():string {
    if(this.pltfrm.is('android')) {
      return "md";
    }
    else if(this.pltfrm.is('ios')) {
      return "ios";
    } else {
      return "md";
    }
  }
  
  renderText(content) {
    content = content.replace(/&nbsp;/mgi, ' ');
    content = content.replace(/&#8221;/mgi, '"');
    content = content.replace(/&#8243;/mgi, '"');
    content = content.replace(/\[/mgi, '<');
    content = content.replace(/\]/mgi, '>');
    content = content.replace(/<vc_row[^<>]*>/mgi, '<ion-row>');
    content = content.replace(/<\/vc_row[^<>]*>/mgi, '</ion-row>');
    content = content.replace(/<vc_column[^<>]*>/mgi, '<ion-col>');
    content = content.replace(/<\/vc_column[^<>]*>/mgi, '</ion-col>');
    content = content.replace(/<vc_column_text[^<>]*>/mgi, '<p>');
    content = content.replace(/<\/vc_column_text[^<>]*>/mgi, '</p>');
    content = content.replace(/<vc_custom_heading[^<>]*>/mgi, '<h4>');
    content = content.replace(/<\/vc_custom_heading[^<>]*>/mgi, '</h4>');
    content = content.replace(/<vc_raw_html[^<>]*>/mgi, '<div class="htmlcustom">');
    content = content.replace(/<\/vc_raw_html[^<>]*>/mgi, '</div>');
    content = "<div>" + content + "</div>";
    const doc2 = pjXML.parse(content);
    return doc2.text();
  }

  render(content: string) {
    content = content.replace(/&nbsp;/mgi, ' ');
    content = content.replace(/&#8221;/mgi, '"');
    content = content.replace(/&#8243;/mgi, '"');
    content = content.replace(/\[/mgi, '<');
    content = content.replace(/\]/mgi, '>');
    content = '<div class="container-fluid">' + content + '</div>';
    const doc = pjXML.parse(content);
    this.parse_row(doc);
    this.parse_column(doc);
    this.parse_column_text(doc);
    this.parse_custom_headings(doc);
    this.parse_nectar_btn(doc);
    this.parse_nectar_video_lightbox_tag(doc);
    this.parse_nectar_gallery(doc);
    const scripts = this.parse_raw_html(doc);
    this.remove_badfromed_tag(doc);
    return { html: doc.xml(), scripts};
  }

  private parse_nectar_gallery(doc: any) {
    let nsldrs = doc.select('//vc_gallery');
    if(!nsldrs) return;
    if(!(nsldrs instanceof Array)) {
      nsldrs = [nsldrs];
    }
    nsldrs.forEach(nsldr => {
      nsldr.name = "div";
      nsldr.content = [];
      let gallery = new pjXML.Node(1);
      gallery.name = "wp-gallery";
      gallery.attributes = {};
      gallery.attributes.apisrc = "https://portamerica.es";
      gallery.attributes.images = nsldr.attributes.images;
      nsldr.content.push(gallery);
    });
  }

  private parse_nectar_video_lightbox_tag(doc: any) {
    let nvls = doc.select('//nectar_video_lightbox');
    if(!nvls) return;
    if(!(nvls instanceof Array)) {
      nvls = [nvls];
    }
    nvls.forEach( nvl => {
      nvl.name = "img";
      nvl.attributes = {
        src: './assets/imgs/youtube.png',
        class: "videoImg",
        href: nvl.attributes.video_url,
        style: 'background-image: url(https://img.youtube.com/vi/' + nvl.attributes.video_url.split('?v=')[1].split('&')[0] + '/0.jpg)'
      };
    });
  }

  private remove_badfromed_tag(node: any) {
    node.content.forEach( (contentNode, index) => {
      if(typeof contentNode == 'string' && contentNode != '') {
        node.content[index] = contentNode.replace(/\/[a-zA-z]+>/mgi, '');
      } else if(typeof contentNode == 'object') {
        this.remove_badfromed_tag(contentNode);
      }
    });
  }

  private parse_raw_html(doc: any) {
    let raws = doc.select('//vc_raw_html');
    let scripts = [];
    if(!raws) return scripts;
    if(!(raws instanceof Array)) {
      raws = [raws];
    }
    raws.forEach( raw => {
      raw.content = pjXML.parse(decodeURIComponent(atob(raw.content[0]))).content;
      raw.name = "div";
      scripts.push(...this.getScriptsNode(raw));
    });
    /*console.log(raws);
    console.log(scripts);*/
    return scripts;
  }

  getScriptsNode(node: any) {
    var scripts = [];
    if(!node || !node.content) return scripts;
    node.content.forEach( childNode => {
      if(childNode.name == 'script') {
        if(childNode.attributes.src) scripts.push(childNode.attributes.src);
        childNode.name = 'span';
        childNode.attributes = {};
      }
      else {
        const sc = this.getScriptsNode(childNode);
        if(sc) scripts.push(...sc);
      }
    });
    return scripts;
  }

  private parse_row(doc: any) {
    let rows = doc.select('//vc_row');
    if(!rows) return;
    if(!(rows instanceof Array)) {
      rows = [rows];
    }
    rows.forEach( row => {
      //row.name = "ion-row";
      row.name = "div";
      row.attributes = {};
      row.attributes.class = "row";
    });
    //console.log(rows, 'rows');
  }

  private parse_column(doc: any) {
    let columns = doc.select('//vc_column');
    if(!columns) return;
    if(!(columns instanceof Array)) {
      columns = [columns];
    }
    columns.forEach( column => {
      //column.name = "ion-col";
      column.name = "div";
      const size = parseFloat(eval(column.attributes.width)) * 12;
      //column.attributes = {};
      column.attributes.class = "col-12 col-md-" + size;
    });
    //console.log(columns, 'colums');
  }

  private parse_column_text(doc: any) {
    let columnsTexts = doc.select('//vc_column_text');
    if(!columnsTexts) return;
    if(!(columnsTexts instanceof Array)) {
      columnsTexts = [columnsTexts];
    }
    columnsTexts.forEach( columnText => {
      columnText.name = "div";
      //columnText.attributes.style = columnText.attributes.css.
    });
    //console.log(columnsTexts, 'columnsTexts');
  }

  private parse_nectar_btn(doc: any) {
    let nectarBtns = doc.select('//nectar_btn');
    if(!nectarBtns) return;
    if(!(nectarBtns instanceof Array)) {
      nectarBtns = [nectarBtns];
    }
    nectarBtns.forEach( nectarBtn => {
      nectarBtn.name = "button";
      const attrs = Object.assign({}, nectarBtn.attributes);
      nectarBtn.attributes = {};
      for(const key in attrs) {
        if(key == 'text') {
          nectarBtn.content.push(attrs[key]);
        }
        else if(key == 'url') {
          nectarBtn.attributes.href = attrs[key];
        }
      }
      nectarBtn.attributes.class = `disable-hover button button-${this.getPlatformMode()} button-default button-default-${this.getPlatformMode()} button-block button-block-${this.getPlatformMode()} button-${this.getPlatformMode()}-dark`;
      const effect = pjXML.parse('<div class="button-effect"></div>');
      nectarBtn.content.push(effect.content[1]);
    });
    //console.log(nectarBtns, 'nectarBtns');
  }

  private parse_custom_headings(doc: any) {//Custom_heading
    let headings = doc.select('//vc_custom_heading');
    if(!headings) {
      return;
    }
    if(!(headings instanceof Array)) {
      headings = [headings];
    }
    headings.forEach( headerElement => {
      const tmpAttr = headerElement.attributes.font_container.split('|');
      let fontAttrs: any = {
        tag: 'h4'
      };
      tmpAttr.forEach((elemento, indice) => {
        let params = elemento.split(':');
        fontAttrs[params[0]] = params[1];
        if(params[0] == 'tag') {
          headerElement.name = params[1];
        }
        else if(CSSAttrs.indexOf(params[0].replace('_','-')) !== -1) {
          if(!headerElement.attributes.style) {
            headerElement.attributes.style = "";
          }
          headerElement.attributes.style += params[0].replace('_','-') + ":" + params[1] + ";";
        }
        //else if() { }
      });
      const html = pjXML.parse(headerElement.attributes.text).content;
      headerElement.content.unshift(...html);//
      delete headerElement.attributes.font_container;
      delete headerElement.attributes.google_fonts;
      delete headerElement.attributes.text;
    }); 
    //console.log(headings, "custom heading");  
  }
}



