import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, Platform } from 'ionic-angular';
import { BackbuttonProvider } from '../../providers/backbutton/backbutton';
import { VisualComposerProvider } from '../../providers/visual-composer/visual-composer';
import { PagesProvider } from '../../providers/pages/pages';

@IonicPage({
  name: 'pagina',
  segment: 'pagina/:id',
  defaultHistory: ['inicio']
})
@Component({
  selector: 'page-pagina',
  templateUrl: 'pagina.html',
})
export class PaginaPage {
  page:any = {
    title: 'Cargando...',
    content: '<h2 style="text-align: center;">Cargando...</h2>',
    isLoading: true,
    image: null,
    imgFull: null,
    scripts: []
  };

  @ViewChild('scriptscontainer') scriptcontainer: ElementRef;
  isfirstLoad = false;
  constructor(public navCtrl: NavController,
              private pageProv: PagesProvider,
              public pltfrm: Platform,
              private navParams: NavParams,
              private toastCtrl: ToastController,
              private bckBtn: BackbuttonProvider,
              private vC: VisualComposerProvider) { }

  ionViewDidLoad() {
    
    if (this.bckBtn.isfirstLoad) {
      console.log('firstload-pagina');
      this.isfirstLoad = true;
      this.bckBtn.isfirstLoad = false;
      const actLoc = window.location.href;
      window.history.replaceState(null, null, '#/inicio');
      window.history.pushState(null, null, actLoc);
    }
    //console.log(this.isfirstLoad, this.bckBtn.isfirstLoad);
    let id = this.navParams.get('id');
    //console.log(id);
    let data = this.navParams.get('data');
    
    if(data) {
      //console.log(data);
      this.page.title = data.title.rendered;
      try{
        this.page.image = data['_embedded']['wp:featuredmedia'][0]['media_details']['sizes']['portfolio-thumb'].source_url;
        this.page.imgFull = data['_embedded']['wp:featuredmedia'][0].source_url;
      } catch(exc) { }
      this.page.isLoading = false;
      const render = this.vC.render(data.content.rendered)
      this.page.content = render.html;
      this.page.scripts = render.scripts;
      this.loadScripts();
    }
    else {
      this.pageProv.getPage(id).then((data:any) => {
        //console.log(data);
        this.page.title = data.title.rendered;
        try{
          this.page.image = data['_embedded']['wp:featuredmedia'][0]['media_details']['sizes']['portfolio-thumb'].source_url;
          this.page.imgFull = data['_embedded']['wp:featuredmedia'][0].source_url;
        } catch(exc) { }
        this.page.isLoading = false;
        const render = this.vC.render(data.content.rendered)
        this.page.content = render.html;
        this.page.scripts = render.scripts;
        this.loadScripts();
        //console.log('loadpost');
      }).catch(err => {
        this.toastCtrl.create({
          message: "No se pudo obtener la información",
          duration: 3000
        }).present();
        this.navCtrl.pop();
        console.log(err);
      });
    }
  }

  loadScripts() {
    this.page.scripts.forEach(script => {
      const eleScript = document.createElement('script');
      eleScript.src = script;
      eleScript.onload = () => {
        window.onload(null);/**/
      };
      this.scriptcontainer.nativeElement.appendChild(eleScript);
    });
  }


  gback() {
    console.log(this.navCtrl.canGoBack());
    if(this.navCtrl.canGoBack()) {
      this.navCtrl.pop();
    }
    else {
      window.history.back();
    }
  }

  openModal() {
    //if(this.page.imgFull) this.navCtrl.push(ImageViwerPage, { src: this.page.imgFull });
  }
}



