import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { timeout } from 'rxjs/operators';

@Injectable()
export class ArtistLoaderProvider {
  private tOut = 7500;
  constructor(public http: HttpClient) { }

  public artistas = [];

  filterArtist(searchTern: string) {
    return (searchTern)?this.artistas.filter(item => item.nombre.toLowerCase().indexOf(searchTern) > -1):this.artistas;
  }

  public loadArtist() {
    return this.http.get('./assets/data/artistas.json').pipe(timeout(this.tOut)).toPromise().then((data:any) => {
      this.artistas = data.artistas;
      return data;
    });
  }

  public getArtist(id: string) {
    for(let artist of this.artistas) {
      if(artist.id === id)
        return artist;
    }
    return null;
  }

}



