import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class MediaLoaderProvider {
  private cacheMedias = [];

  constructor(public http: HttpClient) { }

  getMediaById(id: string): Promise<any> {
    if(this.cacheMedias[id])
      return Promise.resolve(this.cacheMedias[id]);
    else
      return this.http.get('https://portamerica.es/wp-json/wp/v2/media/' + id).toPromise().then((data:any) => {
        this.cacheMedias[id] = data.guid.rendered;
        return data.guid.rendered;
      });
  }

}



