import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { timeout } from 'rxjs/operators';

@Injectable()
export class PagesProvider {

  private tOut = 7500;

  constructor(public http: HttpClient) { }

  getPageBySlug(slug: string) {
    return this.http.get('https://portamerica.es/wp-json/wp/v2/pages?_embed&slug=' + encodeURI(slug)).pipe(timeout(this.tOut)).toPromise();
  }

  getPage(id: string) {
    return this.http.get('https://portamerica.es/wp-json/wp/v2/pages/'  + id + '?_embed').pipe(timeout(this.tOut)).toPromise();
  }
}



