import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ArtistLoaderProvider } from '../../providers/artist-loader/artist-loader';
import { ArtistaPage } from '../artista/artista';
import { BackbuttonProvider } from '../../providers/backbutton/backbutton';
import { FormControl } from '@angular/forms';
import 'rxjs/add/operator/debounceTime';

@IonicPage({
  name: 'artistas',
  segment: 'artistas',
})
@Component({
  selector: 'page-artistas',
  templateUrl: 'artistas.html',
})
export class ArtistasPage {

  artistas = null;
  isLoading = true;
  errorLoading = false;
  searchTerm = "";
  searchControl: FormControl;

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public aL: ArtistLoaderProvider,
              private bkBtn: BackbuttonProvider) {
                this.bkBtn.isfirstLoad = false;
                this.searchControl = new FormControl();
              }

  ionViewDidLoad() {
    this.loadArtistas().then(() => {
      this.artistas = this.aL.artistas;
      this.searchControl.valueChanges.debounceTime(500).subscribe(search  => {
          if(!this.isLoading && !this.errorLoading) {
            this.artistas = this.aL.filterArtist(search);
          } else {
            this.artistas = this.aL.artistas;
          }
        });
    });
  }

  checkInitialChange(item, itemIndex, items){
    if(itemIndex == 0)
      return item.nombre[0];
    if(item.nombre[0] != items[itemIndex-1].nombre[0])
      return item.nombre[0];
    return null;
  }

  loadArtistas() {
    this.isLoading = true;
    return this.aL.loadArtist().then(() => {
      this.searchTerm = "";
      this.isLoading = false;
      this.errorLoading = false;
    }).catch((err) => {
      console.log(err);
      this.isLoading = false;
      this.errorLoading = true;
    });;
  }

  doRefresh(ev) {
    this.loadArtistas().then(() => ev.complete()).catch(() => ev.complete());
  }

  lnkArtista(id:string) {
    this.navCtrl.push(ArtistaPage, { id });
  }
}



