import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { EntradaPage } from '../entrada/entrada';
import { PostsProvider } from '../../providers/posts/posts';
import { BackbuttonProvider } from '../../providers/backbutton/backbutton';
import { VisualComposerProvider } from '../../providers/visual-composer/visual-composer';

@IonicPage({
  name: 'blog',
  segment: 'blog'
})
@Component({
  selector: 'page-blog',
  templateUrl: 'blog.html',
})
export class BlogPage {

  @ViewChild(InfiniteScroll) infiniteScroll: InfiniteScroll;
  posts: any[] = [];
  isLoading = true;
  offset = 0;
  errorLoading = false;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public postProv:PostsProvider,
              private bkBtn: BackbuttonProvider,
              public vC: VisualComposerProvider) {
    this.bkBtn.isfirstLoad = false;
    this.getPosts();
  }

  loadData(event){
    event.waitFor(
      this.postProv.getPosts(this.offset).then((data:any[]) => {
        if (data.length === 0) {
          event.target.disabled = true;
          return;
        }
        this.offset += data.length;
        this.posts.push(...data);
        this.errorLoading = false;

      }).catch( () => {
        this.errorLoading = true;
    }));
  }
  async doRefresh(event){
    this.offset = 0;
    await this.getPosts();
    event.complete();
  }

  private getPosts(): Promise<any> {
    return this.postProv.getPosts(this.offset).then( (data:any[]) => {
      this.posts = [];
      this.isLoading = false;
      this.offset += data.length;
      this.posts.push(...data);
    }).catch( () => {
      this.isLoading = false;
      this.errorLoading = true;
      console.log('err post');
    });
  }

  loadPost(id){
    this.navCtrl.push(EntradaPage, {id: id});
  }

}



