import { Component } from '@angular/core';
import { IonicPage, NavParams } from 'ionic-angular';
import { HorarioDiaPage } from '../horario-dia/horario-dia';
import { HorariosLoaderProvider } from '../../providers/horarios-loader/horarios-loader';
import { SuperTabsController } from 'ionic2-super-tabs';

@IonicPage({
  name: 'horarios',
  segment: 'horarios'
})
@Component({
  selector: 'page-horarios',
  templateUrl: 'horarios.html',
})
export class HorariosPage {
  page: any = HorarioDiaPage;
  title = "Horarios";
  startIndex = 0;
  constructor(public horasProv: HorariosLoaderProvider, private navParams: NavParams, private superTabsCtrl: SuperTabsController) {
    this.horasProv.loadArtist().then(() => {
      if(this.horasProv.horarioInfo) {
        this.title = "Horarios: " + this.horasProv.horarioInfo.dias[0].text;
        const dia = this.navParams.get('dia');
        if(dia) {
          setTimeout(() => {
            this.superTabsCtrl.slideTo(dia);
          }, 500);
        }
      }
        
    });
  }
  onTabSelect(ev: any) {
    if(this.horasProv.horarioInfo)
      this.title = "Horarios: " + this.horasProv.horarioInfo.dias[ev.index].text;
  }
}



