import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { timeout } from 'rxjs/operators';

@Injectable()
export class PostsProvider {
  private cachePostsByID = [];
  private cachePostsBySlug = [];
  private tOut = 7500;
  constructor(public http: HttpClient) { }
  getPosts(offset: number){
    return this.http.get('https://portamerica.es/wp-json/wp/v2/posts?_embed&per_page=10&offset=' + offset).pipe(timeout(this.tOut)).toPromise();
  }
  getPost(id: string){
    if(this.cachePostsByID[id])
      return new Promise((resolve, reject) => {
        resolve(this.cachePostsByID[id]);
      });
    else
      return this.http.get('https://portamerica.es/wp-json/wp/v2/posts/' + id + '?_embed').pipe(timeout(this.tOut)).toPromise().then((data:any) => {
        this.cachePostsByID[id] = data;
        this.cachePostsBySlug[data.slug] = data;
        return data;
      });
  }
  getPostBySlug(slug: string) {
    let parseslug = slug.replace(/^\//, '').replace(/\/$/, '');
    if(this.cachePostsBySlug[parseslug]) {
      return new Promise((resolve, reject) => {
        resolve([this.cachePostsBySlug[parseslug]]);
      });
    }
    else
      return this.http.get('https://portamerica.es/wp-json/wp/v2/posts?_embed&slug=' + encodeURI(slug)).pipe(timeout(this.tOut)).toPromise().then((data:any) => {
        if(data[0]) {
          this.cachePostsByID[data[0].id] = data[0];
          this.cachePostsBySlug[data[0].slug] = data[0];
        }
        return data;
      });
  }
}



