import { Component } from '@angular/core';
import { IonicPage, NavParams } from 'ionic-angular';
import { HorariosLoaderProvider } from '../../providers/horarios-loader/horarios-loader';

@IonicPage()
@Component({
  selector: 'page-horario-dia',
  templateUrl: 'horario-dia.html',
})
export class HorarioDiaPage {
  dia = null;
  colors1 = ['#037006', '#6e7003', '#720b03'];
  colors2 = ['#090270', '#037006', '#6e7003'];
  constructor(public horasProv: HorariosLoaderProvider, private navParams: NavParams) {
    this.dia = this.navParams.get('id');
    if(!this.horasProv.horarioInfo && !this.horasProv.isLoading)
      this.horasProv.loadArtist();
  }


}


