import { ArtistaPage } from "../pages/artista/artista";
import { ArtistasPage } from "../pages/artistas/artistas";
import { BlogPage } from "../pages/blog/blog";
import { ComoLlegarPage } from "../pages/como-llegar/como-llegar";
import { EntradaPage } from "../pages/entrada/entrada";
import { HorariosPage } from "../pages/horarios/horarios";
import { HorarioDiaPage } from "../pages/horario-dia/horario-dia";
import { PaginaPage } from "../pages/pagina/pagina";
import { ShowRockingPage } from "../pages/show-rocking/show-rocking";
import { VentaEntradasPage } from "../pages/venta-entradas/venta-entradas";
import { InicioPage } from "../pages/inicio/inicio";

export const getUrlByComponentPage = (comp: any) => {
    if(comp == InicioPage) return '/inicio';
    if(comp == ArtistaPage) return '/artista';
    if(comp == ArtistasPage) return '/artistas';
    if(comp == BlogPage) return '/blog';
    if(comp == ComoLlegarPage) return '/como-llegar';
    if(comp == EntradaPage) return '/blog';
    if(comp == HorariosPage) return '/horarios';
    if(comp == HorarioDiaPage) return '/horario';
    if(comp == PaginaPage) return '/page';
    if(comp == ShowRockingPage) return '/show-rocking';
    if(comp == VentaEntradasPage) return '/venta-entradas';
    return null;
};


