import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { PostsProvider } from '../providers/posts/posts';
import { BackbuttonProvider } from '../providers/backbutton/backbutton';
import { BlogPageModule } from '../pages/blog/blog.module';
import { EntradaPageModule } from '../pages/entrada/entrada.module';
import { InicioPageModule } from '../pages/inicio/inicio.module';
import { InicioPage } from '../pages/inicio/inicio';
import { BlogPage } from '../pages/blog/blog';
import { EntradaPage } from '../pages/entrada/entrada';
import { NotFoundPageModule } from '../pages/not-found/not-found.module';
import { PaginaPageModule } from '../pages/pagina/pagina.module';
import { PagesProvider } from '../providers/pages/pages';
import { IosStatusProvider } from '../providers/ios-status/ios-status';
import { LinkerProvider } from '../providers/linker/linker';
import { PaginaPage } from '../pages/pagina/pagina';
import { VisualComposerProvider } from '../providers/visual-composer/visual-composer';
import { VentaEntradasPageModule } from '../pages/venta-entradas/venta-entradas.module';
import { VentaEntradasPage } from '../pages/venta-entradas/venta-entradas';
import { LenguajeProvider } from '../providers/lenguaje/lenguaje';
import { ArtistasPageModule } from '../pages/artistas/artistas.module';
import { ArtistasPage } from '../pages/artistas/artistas';
import { ArtistLoaderProvider } from '../providers/artist-loader/artist-loader';
import { ArtistaPageModule } from '../pages/artista/artista.module';
import { ArtistaPage } from '../pages/artista/artista';
import { ComoLlegarPage } from '../pages/como-llegar/como-llegar';
import { ComoLlegarPageModule } from '../pages/como-llegar/como-llegar.module';
import { ImageViewerPage } from '../pages/image-viewer/image-viewer';
import { ImageViewerPageModule } from '../pages/image-viewer/image-viewer.module';
import { HorarioDiaPageModule } from '../pages/horario-dia/horario-dia.module';
import { NoSleepProvider } from '../providers/no-sleep/no-sleep';
import { HorariosLoaderProvider } from '../providers/horarios-loader/horarios-loader';
import { ShowRockingPageModule } from '../pages/show-rocking/show-rocking.module';
import { ShowRockingPage } from '../pages/show-rocking/show-rocking';
import { MediaLoaderProvider } from '../providers/media-loader/media-loader';
import { HorariosPageModule } from '../pages/horarios/horarios.module';
import { HorariosPage } from '../pages/horarios/horarios';
import { InfoInstallPageModule } from '../pages/info-install/info-install.module';
import { InfoInstallPage } from '../pages/info-install/info-install';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {
      backButtonText: '',
      preloadModules: true
    }, {
      links: [
        { component: InicioPage, name: 'inicio', segment: 'inicio' },
        { component: BlogPage, name: 'blog', segment: 'blog' },
        { component: VentaEntradasPage, name: 'venta-entradas', segment: 'venta-entradas' },
        { component: ComoLlegarPage, name: 'como-llegar', segment: 'como-llegar' },
        { component: HorariosPage, name: 'horarios', segment: 'horarios' },
        { component: ShowRockingPage, name: 'show-rocking', segment: 'show-rocking' },
        { component: ArtistasPage, name: 'artistas', segment: 'artistas' },
        { component: ArtistaPage, name: 'artista', segment: 'artista/:id', defaultHistory: [ArtistasPage] },
        { component: EntradaPage, name: 'entrada', segment: 'blog/:id', defaultHistory: [BlogPage] },
        { component: PaginaPage, name: 'pagina', segment: 'pagina/:id', defaultHistory: [InicioPage] },
        { component: ImageViewerPage, name: 'image-viewer', segment: 'image-viewer/:src', defaultHistory: [BlogPage] },
        //{ component: NotFoundPage, name: 'not-found', segment: ':any', defaultHistory: [InicioPage] },
        { component: InfoInstallPage, name: 'info-install', segment: 'info-install' },
      ]
    }),
    HttpClientModule,
    InicioPageModule,
    BlogPageModule,
    EntradaPageModule,
    PaginaPageModule,
    NotFoundPageModule,
    VentaEntradasPageModule,
    ArtistasPageModule,
    ArtistaPageModule,
    ComoLlegarPageModule,
    ImageViewerPageModule,
    HorariosPageModule,
    HorarioDiaPageModule,
    ShowRockingPageModule,
    InfoInstallPageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    PostsProvider,
    BackbuttonProvider,
    LinkerProvider,
    IosStatusProvider,
    PagesProvider,
    VisualComposerProvider,
    LenguajeProvider,
    ArtistLoaderProvider,
    NoSleepProvider,
    HorariosLoaderProvider,
    MediaLoaderProvider
  ]
})
export class AppModule { }



