import { Injectable } from '@angular/core';
import { ToastController, App } from 'ionic-angular';
import { PagesProvider } from '../pages/pages';
import { PostsProvider } from '../posts/posts';
import { EntradaPage } from '../../pages/entrada/entrada';
import { PaginaPage } from '../../pages/pagina/pagina';
import { ImageViewerPage } from '../../pages/image-viewer/image-viewer';

const imageTypes = ['jpg', 'png', 'jpeg', 'gif'];
const newTabTypes = ['pdf', 'txt'];
const customRedirect = {
  '/#entradas2019': {
    url: 'venta-entradas',
    params: {}
  },
  '/entradas': {
    url: 'venta-entradas',
    params: {}
  },
  '/entradas/': {
    url: 'venta-entradas',
    params: {}
  },
  '/showrocking-2018/': {
    url: 'show-rocking',
    params: {}
  },
  '/artistas/': {
    url: 'artistas',
    params: {}
  }
};

@Injectable()
export class LinkerProvider {
  constructor(private pages: PagesProvider,
              private posts: PostsProvider,
              private toastCtrl: ToastController,
              private app: App) { }

  private findParent(tagname,el) {
    while (el){
      if ((el.nodeName || el.tagName).toLowerCase()===tagname.toLowerCase()){
        return el;
      }
      el = el.parentNode;
    }
    return null;
  }

  public init() {
    document.body.addEventListener('click', (e:any) => {
      let nav = this.app.getActiveNavs()[0];
      const a = this.findParent('a', e.target);
      if (a && a.href) {
        // any custom pages
        if(a.href.startsWith('http://portamerica.es') || a.href.startsWith('https://portamerica.es')){
          //console.log('app open: ' + a.href);
          const slug = a.href.replace('http://portamerica.es', '').replace('https://portamerica.es', '');
          const extension: string = slug.split('.').pop();
          if(customRedirect[slug]) {
            nav.push(customRedirect[slug].url);
          } else if(imageTypes.indexOf(extension.toLocaleLowerCase()) != -1) {
              this.openImage(a.href);
          } else if(newTabTypes.indexOf(extension.toLocaleLowerCase()) != -1) {
            window.open(a.href, '_blank');
          } else {
            let toast = this.toastCtrl.create({
              message: 'Cargando...',
              cssClass: 'loadingToast'
            });
            toast.present();
            Promise.all([this.posts.getPostBySlug(slug), this.pages.getPageBySlug(slug)]).then( (values: any) => {
              // console.log(values);
              if(values[0].length) {
                nav.push(EntradaPage, { id: values[0][0].id, data: values[0][0] });
              }
              else if(values[1].length) {
                nav.push(PaginaPage, { id: values[1][0].id, data: values[1][0] });
              }
              toast.dismiss();
            }).catch(err => {
              console.log(err);
              toast.dismiss();
              this.toastCtrl.create({
                message: 'No se ha podido obtener la información',
                duration: 2000,
                closeButtonText: "OK",
                showCloseButton: true
              }).present();
            });
          }
        }
        else
          window.open(a.href, '_blank');
        e.preventDefault();
        e.stopPropagation();
        return false;
      } else {
        const btn = this.findParent('button', e.target);
        if(btn && btn.getAttribute('href')) {
          if(btn.getAttribute('href').startsWith('http://portamerica.es') || btn.getAttribute('href').startsWith('https://portamerica.es')) {
            //console.log('app open: ' + btn.getAttribute('href'));
            let slug = btn.getAttribute('href').replace('http://portamerica.es', '').replace('https://portamerica.es', '');
            const extension: string = slug.split('.').pop();
            //console.log(extension);
            if(customRedirect[slug]) {
              nav.push(customRedirect[slug].url);
            } else if(imageTypes.indexOf(extension.toLocaleLowerCase()) != -1) {
              this.openImage(btn.getAttribute('href'));
            } else if(newTabTypes.indexOf(extension.toLocaleLowerCase()) != -1) {
              window.open(btn.getAttribute('href'), '_blank');
            } else {
              let toast = this.toastCtrl.create({
                message: 'Cargando...',
                cssClass: 'loadingToast'
              });
              toast.present();
              Promise.all([this.posts.getPostBySlug(slug), this.pages.getPageBySlug(slug)]).then( (values: any) => {
                // console.log(values);
                if(values[0].length) {
                  nav.push(EntradaPage, { id: values[0][0].id, data: values[0][0] });
                }
                else if(values[1].length) {
                  nav.push(PaginaPage, { id: values[1][0].id, data: values[1][0] });
                } else {
                  //error
                }
                toast.dismiss();
              }).catch(err => {
                console.log(err);
                toast.dismiss();
                this.toastCtrl.create({
                  message: 'No se ha podido obtener la información',
                  duration: 2000,
                  closeButtonText: "OK",
                  showCloseButton: true
                }).present();
              });
            }
            
          }
          else
            window.open(btn.getAttribute('href'), '_blank');
          e.preventDefault();
          e.stopPropagation();
          return false;
        } else {
          const img = this.findParent('img', e.target);
          if(img && img.getAttribute('href')) {
            if(img.getAttribute('href').startsWith('http://portamerica.es') || img.getAttribute('href').startsWith('https://portamerica.es')) {
              //console.log('app open: ' + img.getAttribute('href'));
              let slug = img.getAttribute('href').replace('http://portamerica.es', '').replace('https://portamerica.es', '');
              const extension: string = slug.split('.').pop();
              //console.log(extension);
              if(customRedirect[slug]) {
                nav.push(customRedirect[slug].url);
              } else if(imageTypes.indexOf(extension.toLocaleLowerCase()) != -1) {
                this.openImage(img.getAttribute('href'));
              } else if(newTabTypes.indexOf(extension.toLocaleLowerCase()) != -1) {
                window.open(img.getAttribute('href'), '_blank');
              } else {
                let toast = this.toastCtrl.create({
                  message: 'Cargando...',
                  cssClass: 'loadingToast'
                });
                toast.present();
                Promise.all([this.posts.getPostBySlug(slug), this.pages.getPageBySlug(slug)]).then( (values: any) => {
                  // console.log(values);
                  if(values[0].length) {
                    nav.push(EntradaPage, { id: values[0][0].id, data: values[0][0] });
                  }
                  else if(values[1].length) {
                    nav.push(PaginaPage, { id: values[1][0].id, data: values[1][0] });
                  } else {
                    //error
                  }
                  toast.dismiss();
                }).catch(err => {
                  console.log(err);
                  toast.dismiss();
                  this.toastCtrl.create({
                    message: 'No se ha podido obtener la información',
                    duration: 2000,
                    closeButtonText: "OK",
                    showCloseButton: true
                  }).present();
                });
              }
              
            }
            else
              window.open(img.getAttribute('href'), '_blank');
            e.preventDefault();
            e.stopPropagation();
            return false;
          } else if(e.target.tagName == 'WP-GALLERY' && e.path[0] && e.path[0].tagName == 'IMG') {
            if(e.path[0].src.startsWith('http://portamerica.es') || e.path[0].src.startsWith('https://portamerica.es')) {
              this.openImage(e.path[0].src);
            }
            else {
              window.open(e.path[0].src, '_blank');
            }
            e.preventDefault();
            e.stopPropagation();
            return false; 
          }
        }
      }
      return true;
    });
  }

  public openImage(src: string) {
    let nav = this.app.getActiveNavs()[0];
    if(nav) {
      nav.push(ImageViewerPage, { src });
    }
  }

}



