import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { timeout } from 'rxjs/operators';

@Injectable()
export class HorariosLoaderProvider {

  private tOut = 7500;
  constructor(public http: HttpClient) { }

  public horarioInfo:any = null;
  public isLoading = false;
  public errLoading = false;

  public loadArtist() {
    this.horarioInfo = null;
    this.isLoading = true;
    return this.http.get('./assets/data/horarios.json').pipe(timeout(this.tOut)).toPromise().then((data:any) => {
      this.horarioInfo = data;
      this.isLoading = false;
      return data;
    }).catch(err => {
      this.errLoading = true;
    });
  }

}



