import { Component, Input } from '@angular/core';
import { App } from 'ionic-angular';

@Component({
  selector: 'horario-timeline',
  templateUrl: 'horario-timeline.html'
})
export class HorarioTimelineComponent {
  @Input('es1Name') es1Name:string;
  @Input('es2Name') es2Name:string;
  @Input('color1') color1:string;
  @Input('color2') color2:string;
  @Input('events') events:any[];
  constructor(public appCtrl: App) { }

}



