import { Component, ElementRef, ViewChild, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, Platform } from 'ionic-angular';
import { PostsProvider } from '../../providers/posts/posts';
import { BackbuttonProvider } from '../../providers/backbutton/backbutton';
import { VisualComposerProvider } from '../../providers/visual-composer/visual-composer';

@IonicPage({
  name: 'entrada',
  segment: 'blog/:id',
  defaultHistory: ['blog']
})
@Component({
  selector: 'page-entrada',
  templateUrl: 'entrada.html'
})
export class EntradaPage {
  post:any = {
    title: 'Cargando...',
    content: '<h2 style="text-align: center;">Cargando...</h2>',
    isLoading: true,
    image: null,
    imgFull: null,
    scripts: []
  };
  
  showToolbar:boolean = false;
  @ViewChild('scriptscontainer') scriptcontainer: ElementRef;

  constructor(public navCtrl: NavController,
              public pltfrm: Platform,
              private navParams: NavParams,
              private postProv: PostsProvider,
              private toastCtrl: ToastController,
              private bckBtn: BackbuttonProvider,
              private vC: VisualComposerProvider,
              public ref: ChangeDetectorRef) { }

  ionViewDidLoad() {

    if (this.bckBtn.isfirstLoad) {
      console.log('firstload-entrada');
      this.bckBtn.isfirstLoad = false;
      const actLoc = window.location.href;
      window.history.replaceState(null, null, '#/blog');
      window.history.pushState(null, null, actLoc);
    }
    let id = this.navParams.get('id');
    let data = this.navParams.get('data');
    if(data) {
      this.post.title = data.title.rendered;
      try{
        this.post.image = data['_embedded']['wp:featuredmedia'][0]['media_details']['sizes']['portfolio-thumb'].source_url;
        this.post.imgFull = data['_embedded']['wp:featuredmedia'][0].source_url;
      } catch(exc) { }
      this.post.isLoading = false;
      const render = this.vC.render(data.content.rendered)
      this.post.content = render.html;
      this.post.scripts = render.scripts;
      this.loadScripts();
    }
    else {
      this.postProv.getPost(id).then((data:any) => {
        this.post.title = data.title.rendered;
        try{
          this.post.image = data['_embedded']['wp:featuredmedia'][0]['media_details']['sizes']['portfolio-thumb'].source_url;
          this.post.imgFull = data['_embedded']['wp:featuredmedia'][0].source_url;
        } catch(exc) { }
        this.post.isLoading = false;
        const render = this.vC.render(data.content.rendered)
        this.post.content = render.html;
        this.post.scripts = render.scripts;
        this.loadScripts();
      }).catch(err => {
        this.toastCtrl.create({
          message: "No se pudo obtener la información",
          duration: 3000
        }).present();
        this.navCtrl.pop();
        console.log(err);
      });
    }

    /*this.menuCtrl.enable(false, 'myMenu');
    this.navCtrl.swipeBackEnabled = true;*/
  }

  onScroll($event: any){
    let scrollTop = $event.scrollTop;
    this.showToolbar = scrollTop >= 120;
    this.ref.detectChanges();
  }

  loadScripts() {
    this.post.scripts.forEach(script => {
      const eleScript = document.createElement('script');
      eleScript.src = script;
      this.scriptcontainer.nativeElement.appendChild(eleScript);
    });
  }

  ionViewWillUnload() {
    /*this.menuCtrl.enable(true, 'myMenu');
    this.navCtrl.swipeBackEnabled = false;*/
  }

  /*getWidth():number {
    return window.screen.availWidth;
  }*/

  gback() {
    //console.log(this.navCtrl.canGoBack());
    if(this.navCtrl.canGoBack()) {
      this.navCtrl.pop();
    }
    else {
      window.history.back();
    }
  }

  openModal() {
    //if(this.post.imgFull) this.imageViewerCtrl.create(this.post.imgFull).present();
    //this.navCtrl.push(ImageViwerPage, { src: this.post.imgFull });
  }
}



