import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, AlertController, LoadingController, ViewController, App, ModalController } from 'ionic-angular';

import { BlogPage } from '../pages/blog/blog';
import { InicioPage } from '../pages/inicio/inicio';
import { LinkerProvider } from '../providers/linker/linker';
import { IosStatusProvider } from '../providers/ios-status/ios-status';
import { VentaEntradasPage } from '../pages/venta-entradas/venta-entradas';
import { ArtistasPage } from '../pages/artistas/artistas';
import { ComoLlegarPage } from '../pages/como-llegar/como-llegar';
import { HorariosPage } from '../pages/horarios/horarios';
import { InfoInstallPage } from '../pages/info-install/info-install';
import { getUrlByComponentPage } from '../utils/utils';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = InicioPage;
  instalable = false;
  prompt = null;

  pages: Array<{ title: string, component: any, icon: string, active: boolean, params:any, classCss: string }>;

  constructor(public platform: Platform,
              private alertCtrl: AlertController,
              private loadingCtrl: LoadingController,
              private lnkr: LinkerProvider,
              private iosStatus: IosStatusProvider,
              private modalCtrl: ModalController,
              private app: App) {
    this.initializeApp();

    this.pages = [
      { title: 'Inicio', component: InicioPage, icon: "", active: false, params: {}, classCss: "fa-home" },
      { title: 'Blog', component: BlogPage, icon: "list-box", active: false, params: {}, classCss: "" },
      { title: 'Entradas', component: VentaEntradasPage, icon: "", active: false, params: {}, classCss: "fa-ticket" },
      { title: 'Artistas', component: ArtistasPage, icon: "", active: false, params: {}, classCss: "fa-star" },
      { title: 'Cómo llegar', component: ComoLlegarPage, icon: "", active: false, params: {}, classCss: "fa-map" },
      //{ title: 'Show Rocking', component: ShowRockingPage, icon: "", active: false, params: {}, classCss: "fa-cutlery" },
      { title: 'Horarios', component: HorariosPage, icon: "clock", active: false, params: {}, classCss: "" },
      //{ title: 'Camping', component: PaginaPage, icon: "leaf", active: false, params: { id: 3164} },
    ];

  }

  installApp() {
    if(this.prompt) {
      this.prompt.prompt();
      this.prompt.userChoice.then((choiceResult: any) => {
        if (choiceResult.outcome === 'accepted') {
          this.instalable = false;
          this.prompt = null;
        } else { }
      });
    }
  }

  initializeApp() {
    this.app.viewDidLoad.subscribe((view: ViewController) => {
      const parms = (view.data.id == 0)?'0':view.data.id || view.data.src || '';
      const baseUrl = getUrlByComponentPage(view.component);
      if(baseUrl) {
        if(parms) {
          console.log(baseUrl + '/' + parms);
        } else {
          console.log(baseUrl);
        }
      }
        
    });
    this.platform.ready().then(() => {
      this.iosStatus.init();
      this.lnkr.init();
      this.nav.viewDidEnter.subscribe((view: ViewController) => {
        for(let p of this.pages) {
          p.active = (p.component == view.component);
        }
      });
      
      if(this.platform.is('ios')) {
        navigator.serviceWorker.getRegistrations().then(registrations => {
          console.log(registrations);
        });
        this.modalCtrl.create(InfoInstallPage, null, { cssClass: 'inset-modal' }).present();
      } else if(this.platform.is('android')) {
        window.addEventListener('appInstall', (e:any) => {
          this.prompt = e.detail;
          this.instalable = !window.matchMedia('(display-mode: standalone)').matches;
        });
      }
      
      window.addEventListener('appUpdate', (e:any) => {
        let alert = this.alertCtrl.create({
          title: 'Actualización',
          message: 'La App se reiniciará automáticamente',
          enableBackdropDismiss: false,
          buttons: [
            {
              text: 'Actualizar',
              handler: () => {
                let loading = this.loadingCtrl.create({
                  spinner: 'crescent'
                });
                loading.present();
                e.detail.postMessage({ updateSw: true });
              }
            }
          ]
        });
        alert.present();
      }, false);
    });
  }

  openPage(page) {
    if(page.component == this.nav.getActive().component){ }
    else{
      this.nav.setRoot(page.component, page.params);
    }
  }
}



