import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { BackbuttonProvider } from '../../providers/backbutton/backbutton';
import { ArtistLoaderProvider } from '../../providers/artist-loader/artist-loader';

declare var Granim: any;

@IonicPage({
  name: 'artista',
  segment: 'artista/:id',
})
@Component({
  selector: 'page-artista',
  templateUrl: 'artista.html',
})
export class ArtistaPage {
  artistInfo = {
    nombre: 'Cargando...',
    descripcion: 'Cargando...',
    images: null,
    menuimage: null
  };

  @ViewChild('artCanvas') canvasEl: ElementRef;
  gradient:any = null;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private bckBtn: BackbuttonProvider,
              private lA: ArtistLoaderProvider,
              private toastCtrl: ToastController) {
    if (this.bckBtn.isfirstLoad) {
      console.log('firstload-pagina');
      this.bckBtn.isfirstLoad = false;
      const actLoc = window.location.href;
      window.history.replaceState(null, null, '#/artistas');
      window.history.pushState(null, null, actLoc);
    }
  }

  private htmlEntities(data: string):string {
    const ele = document.createElement('span');
    ele.innerHTML = data;
    return ele.textContent;
  }

  ionViewDidLoad() {
    const id = this.navParams.get('id');
    console.log(id);
    if(id) {
      if(this.lA.artistas.length == 0) {
        this.lA.loadArtist().then( ()=> {
          const tmpArt = this.lA.getArtist(id);
          if(tmpArt) {
            this.artistInfo = tmpArt;
            this.artistInfo.descripcion = this.htmlEntities(this.artistInfo.descripcion);
            this.loadGradient();
          } else {
            this.navCtrl.pop();
            this.toastCtrl.create({
              message: 'Información no disponible',
              duration: 1500,
              closeButtonText: "OK",
              showCloseButton: true
            }).present();
          }
          
        });
      } else {
        const tmpArt = this.lA.getArtist(id);
        if(tmpArt) {
          this.artistInfo = tmpArt;
          this.artistInfo.descripcion = this.htmlEntities(this.artistInfo.descripcion);
          this.loadGradient();
        } else {
          this.navCtrl.pop();
          this.toastCtrl.create({
            message: 'Información no disponible',
            duration: 1500,
            closeButtonText: "OK",
            showCloseButton: true
          }).present();
        }
        
        //console.log(this.artistInfo);
      }
    }
    else {
      this.navCtrl.pop();
      this.toastCtrl.create({
        message: 'Información no disponible',
        duration: 1500,
        closeButtonText: "OK",
        showCloseButton: true
      }).present();
    }
  }

  loadGradient() {
    if(this.artistInfo.menuimage) {
      this.gradient = new Granim({
        element: this.canvasEl.nativeElement,
        direction: 'diagonal',
        isPausedWhenNotInView: true,
        image : {
            source: this.artistInfo.menuimage,
            blendingMode: 'multiply',
            stretchMode: ['stretch', 'stretch']
        },
        states : {
            "default-state": {
                gradients: [
                    ['#6a9113', '#fff'],
                    ['#fff', '#6a9113']
                ]
            }
        }
      });
    }
  }

  ionViewWillUnload() {
    if(this.gradient) this.gradient.destroy();
  }
}



