import { Directive, ElementRef, EventEmitter, HostListener, Input, Output } from '@angular/core';
import { LinkerProvider } from '../providers/linker/linker';


@Directive({
	selector: '[imageViewer]'
})
export class ImageViewerDirective {

	@Input('imageViewer') src: string;
	@Output() close = new EventEmitter();

	constructor(
		private _el: ElementRef,
		private lnk: LinkerProvider
	) { }

	@HostListener('click', ['$event']) onClick(event: Event): void {
		event.stopPropagation();
		const element = this._el.nativeElement;
		const imagesrc = this.src || element.src;
		if(imagesrc) this.lnk.openImage(imagesrc);
	}
}



