import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, Slides } from 'ionic-angular';
import { BackbuttonProvider } from '../../providers/backbutton/backbutton';
import { HorariosPage } from '../horarios/horarios';

@IonicPage({
  name: 'inicio',
  segment: 'inicio'
})
@Component({
  selector: 'page-inicio',
  templateUrl: 'inicio.html',
})
export class InicioPage {
  @ViewChild(Slides) slds: Slides;
  constructor(public navCtrl: NavController, private bckBtn: BackbuttonProvider) {
    this.bckBtn.isfirstLoad = false;
  }
  
  ionViewDidLoad(){
    //this.slds.autoplayDisableOnInteraction = false;
  }

  openHorario(dia: number) {
    this.navCtrl.setRoot(HorariosPage, { dia });
  }

}



