import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BackbuttonProvider } from '../../providers/backbutton/backbutton';

/**
 * Generated class for the NotFoundPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'not-found',
  segment: ':any',
  defaultHistory: ['inicio']
})
@Component({
  selector: 'page-not-found',
  templateUrl: 'not-found.html',
})
export class NotFoundPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, private bckBtn: BackbuttonProvider) {
    if (this.bckBtn.isfirstLoad) {
      this.bckBtn.isfirstLoad = false;
      const actLoc = window.location.href;
      window.history.replaceState(null, null, '#/inicio');
      window.history.pushState(null, null, actLoc);
    }
  }
}



