import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage } from 'ionic-angular';

@IonicPage({
  name: 'venta-entradas',
  segment: 'venta-entradas'
})
@Component({
  selector: 'page-venta-entradas',
  templateUrl: 'venta-entradas.html',
})
export class VentaEntradasPage {
  isLoading = true;
  @ViewChild('scriptscontainer') scriptcontainer: ElementRef;
  constructor() { }

  ionViewDidLoad() {
    if(!(<any>window).EBWidgets) {
      const eleScript = document.createElement('script');
      eleScript.src = "https://www.eventbrite.com/static/widgets/eb_widgets.js";
      eleScript.onload = () => {

        (<any>window).EBWidgets.createWidget({
            // Required
            widgetType: 'checkout',
            eventId: '53638109028',
            iframeContainerId: 'eventbrite-widget-container-53638109028',
    
            // Optional
            iframeContainerHeight: 425,  // Widget height in pixels. Defaults to a minimum of 425px if not provided
        });
        this.frmLoad();
      };
    this.scriptcontainer.nativeElement.appendChild(eleScript);

    } else {
      (<any>window).EBWidgets.createWidget({
        // Required
        widgetType: 'checkout',
        eventId: '53638109028',
        iframeContainerId: 'eventbrite-widget-container-53638109028',

        // Optional
        iframeContainerHeight: 425,  // Widget height in pixels. Defaults to a minimum of 425px if not provided
      });
      this.frmLoad();
    }
  }

  frmLoad() {
    this.isLoading = false;
  }
}



